@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,300;1,800&display=swap");

body {
  margin: 0;
  font-family: "Poppins", sans-serif !important;
  margin-bottom: 40px;
  padding-right: 0px !important;
  padding: 0px !important;
  background-color: #F6F7FA!important;
  max-width: 100% !important;
}


.div {
  max-width: 100% !important;
}

.navbar {
  background-color: #31e197;
  font-weight: bold;
}

p.raices {
  font-size: 5rem;
}

.cards {
  width: 25rem;
  box-shadow: 0px 2px 10px grey;
  border-radius: 10px !important;
  margin: 10px;
  justify-content: center;
  align-content: center;
}

.card-top{
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
}
.boton-about {
  width: 250px;
  height: 50px;
  margin: 15px;
  color: #fff;
  background-color: #0090ff;
  border-color: #17b85a;
  border-radius: 50px;
  border-width: 0ch;
  transition: all 0.3s ease-out;
  text-align: center !important;
  align-items: center !important;
}

.boton-about:hover {
  width: 300px;
  color: black;
  background-color: #5fea88;
  border-color: #5fea88;
  text-align: center !important;
  align-items: center !important;
}

.btn-btn {
  width: 30%;
  font-size: larger;
  padding: 25px 70px;
  color: #fff;
  background-color: #0090ff;
  border-color: #17b85a;
  border-radius: 50px;
  border-width: 0ch;
  transition: all 0.3s ease-out;
}

.btn-btn:hover {
  text-align: center;
 width: 40%;
  color: #fff;
  background-color: #5fea88;
  border-color: #5fea88;
}

.btn-tarjeta {
  width: 50%;
  font-size: larger;
  padding: 15px 30px;
  color: #fff;
  background-color: #0090ff;
  border-color: #17b85a;
  border-radius: 50px;
  border: none;
  transition: all 0.3s ease-out;
}

.btn-tarjeta:hover {
  width: 70%;
  color: #fff;
  background-color: #5fea88;
  border-color: #5fea88;
}

.boton-arriba {
  border-radius: 20rem;
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 0;
  margin-right: 30px;
  margin-bottom: 30px;
  width: 50px;
  height: 50px;
  background-color: #0090ff;
  color: #fff;
  border-color: #5fea88;
  border-width: 0ch;
  transition: all 0.3s ease-out;
  z-index: 100;
}
.boton-arriba:hover {
  width: 70px;
  background-color: #0090ff;
  border-color: #5fea88;
}

.section1 {
  align-items: center;
}

.section1 h1,
h2,
p {
  text-align: center;
}

.col-sec1 {
  margin-top: 150px;
}

.section3 {
  background-color:#F6F7FA;
}

.section4 {
  background-color: #F6F7FA;
}

.redes {
  margin: 20px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.redes:hover{
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.thanx {
  width: 30em;
  max-width: 90%;
}


iframe {
  width: 400px;
  height: 450px;
}

.footer {
  background-color: #17b85a;
  font-size: x-large;
  color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  position: relative;
}

.text-mutted {
  font-size: small;
}

.anim-container {
  height: 300px;
  margin-bottom: 10px;
}

.nosotros{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-self: center;
}

@media screen and (max-width: 411px) {

  p.description{
    font-size: 16px;
  }
  .img-vol-coor {
    height: 243px;
  }

  .btn-btn {
    width: 80%;
    padding: 15px 30px;
    margin-top: 10px;
  }

  .btn-tarjeta{
    width: 70%;
  }

  .flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  iframe {
    width: 100%;
    height: 250px;
    margin: 0 auto;
    padding: 0;
  }

}

@media screen and (min-width:412px)and (max-width:768px){

  .btn-btn{
   width: 50%;
   padding: 10px;
   font-size: x-large;
   text-align: center;
   justify-content: center;
   align-items: center;
   align-self: center;
  }

  .boton-arriba{
    width: 70px;
    height: 70px;
  }
  
  .btn-tarjeta{
    font-size: larger;
  }
}

@media screen and (min-width:769px){
  .btn-btn{
    width: 300px;
    padding: 20px;
  }

  .btn-link{
    font-size: x-large !important;
  }
}